app.controller('WelcomeController', ['$scope', '$window', 'translations', 'trackingService',
  function ($scope, $window, translations, trackingService) {
    window.AP.getLocation(location => { trackingService.trackNavigated($scope, location) })
    init()

    $scope.isTimesheet = $window.isTimesheet
    $scope.appNameTranslated = translations[$scope.appName]

    function init () {
      $scope.contentTemplate = function () {
        return $scope.templates['welcome'] // furl
      }
    }
  }
])
